import { ComponentProps, Fragment, Ref } from 'preact';
import { forwardRef } from 'preact/compat';
import { MutableRef } from 'preact/hooks';
import { FocusableHandle, useFocusableTextField } from '../hooks/UNSAFE_useFocusableTextField';
import { useFormContext } from '../hooks/UNSAFE_useFormContext';
import { FormFieldContext } from '../hooks/UNSAFE_useFormFieldContext';
import { useLoadingIndicatorTimer } from '../hooks/UNSAFE_useLoadingIndicatorTimer';
import { useTextField } from '../hooks/UNSAFE_useTextField';
import { DataState } from '../UNSAFE_Collection';
import { Flex } from '../UNSAFE_Flex';
import { Label } from '../UNSAFE_Label';
import { ListView } from '../UNSAFE_ListView';
import { LiveRegion } from '../UNSAFE_LiveRegion';
import {
  ReadonlyTextField,
  ReadonlyTextFieldInput,
  TextField,
  TextFieldInput
} from '../UNSAFE_TextField';
import { InlineUserAssistance } from '../UNSAFE_UserAssistance';
import { Item } from '../utils/UNSAFE_dataProvider';
import { Size } from '../utils/UNSAFE_size';
import { ValueUpdateDetail } from '../utils/UNSAFE_valueUpdateDetail';
import {
  DefaultList,
  Dropdown,
  DropdownArrow,
  DropdownList,
  DropdownUserAssistance,
  EmptyResults,
  isBeforeDataFetch,
  isMobile
} from '../PRIVATE_SelectCommon';
import { ItemTextType } from '../utils/UNSAFE_selectUtils';
import { SelectedValuesCount } from './SelectedValuesCount';
import { useDisplayValues } from './useDisplayValues';
import { useSelectMultiple } from './useSelectMultiple';
import { useTranslationBundle } from '../hooks/UNSAFE_useTranslationBundle';
import { BundleType } from '../resources/nls/bundle';

type TextFieldInputProps = ComponentProps<typeof TextFieldInput>;
type InlineUserAssistanceProps = ComponentProps<typeof InlineUserAssistance>;

type Props<K extends string | number, D extends Record<string, any>> = {
  /**
   * The ID of an element (or space separated IDs of multiple elements) that
   * describes the input.
   */
  ariaDescribedBy?: TextFieldInputProps['ariaDescribedBy'];
  /**
   * Text to provide guidance to help the user understand what data to enter or select.
   */
  assistiveText?: InlineUserAssistanceProps['assistiveText'];
  /**
   * Specifies data for the dropdown list.
   *
   * Note that the <code>itemText</code> property allows for customizing the rendering
   * of each data item.
   */
  data?: DataState<K, D> | null;
  /**
   * Help source URL associated with the component.
   */
  helpSourceLink?: InlineUserAssistanceProps['helpSourceLink'];
  /**
   * Custom text to be rendered for the <code>helpSourceLink</code>.
   */
  helpSourceText?: InlineUserAssistanceProps['helpSourceText'];
  /**
   * Specifies whether the component is disabled.
   */
  isDisabled?: boolean;
  /**
   * Specifies whether the component is waiting for <code>valueItems</code> to load.
   * While loading, SelectMultiple will show the progressive loading indicator in the text field.
   */
  isLoading?: boolean;
  /**
   * Specifies whether the component is readonly.
   */
  isReadonly?: boolean;
  /**
   * Specifies for accessibility purposes whether a value is required.
   *
   * Setting this property to <code>false</code> means that a value is not required to be
   * selected by the user.  Setting this property to <code>true</code> means that a value
   * is required to be selected by the user.
   */
  isRequired?: TextFieldInputProps['isRequired'];
  /**
   * Specifies whether to show an indicator on screen that a value is required, for example
   * before the user has selected a value.
   */
  isRequiredShown?: InlineUserAssistanceProps['isRequiredShown'];
  /**
   * Specifies how to get the text string to render for a data item.
   * This property can be set to either:
   * <ul>
   * <li>a string that specifies the name of a top level data attribute to render as text, or</li>
   * <li>a callback function that takes a properties object and returns the text string to
   * display.</li>
   * </ul>
   *
   * This text will be rendered for the selected <code>valueItems</code> of the component.
   * It will also be rendered for each item in the dropdown.  When rendered for the
   * dropdown items, default matching search term highlighting will still be applied.
   */
  itemText: ItemTextType<K, D>;
  /**
   * Specifies the label associated with the field.
   */
  label: string;
  /**
   * Specifies where the label is positioned relative to the field.
   */
  labelEdge?: 'inside' | 'start' | 'top' | 'none';
  /**
   * Specifies the width of the label when <code>labelEdge</code> is <code>"start"</code>
   */
  labelStartWidth?: Size;
  /**
   * Messages to show on screen that are associated with the component.
   */
  messages?: InlineUserAssistanceProps['messages'];
  /**
   * A short hint to display before the user selects or enters a value.
   */
  placeholder?: TextFieldInputProps['placeholder'];
  /**
   * Specifies how to align text within the field.
   */
  textAlign?: TextFieldInputProps['textAlign'];
  /**
   * Specifies the density of the user assistance presentation.  It can be set to:
   * <ul>
   * <li><code>'efficient'</code>: Show inline and reserve space to prevent layout reflow when user
   * assistance text is displayed.</li>
   * <li><code>'reflow'</code>: Show inline.  Layout will reflow when text is displayed.</li>
   * </ul>
   */
  userAssistanceDensity?: InlineUserAssistanceProps['userAssistanceDensity'];
  /**
   * Specifies the keys, data, and optional metadata for the selected values.
   */
  valueItems?: Item<K, D>[];
  /**
   * The style variant of the component.
   */
  variant?: TextFieldInputProps['variant'];
  /**
   * The type of virtual keyboard to display for entering a value on mobile browsers.
   * This property has no effect on desktop browsers.
   */
  virtualKeyboard?: 'auto' | 'email' | 'number' | 'search' | 'tel' | 'text' | 'url';
  /**
   * Callback invoked when the user commits the selected values.
   *
   * The function should set a new array as the <code>valueItems</code> property.
   */
  onCommit: (detail: ValueUpdateDetail<Set<K>>) => void;
  /**
   * Callback function to trigger loading data for the dropdown list, which may or may not be
   * filtered by user entered search text.
   *
   * The function should set a new <code>DataState</code> as the <code>data</code> property.
   */
  onFilter?: ({ searchText }: { searchText?: string }) => void;
  /**
   * Callback function to handle when the viewport of the dropdown list has changed, for example
   * by scrolling, including the case where the user scrolls to the end of the list and there are
   * more items to load.
   *
   * The function should set a new <code>DataState</code> for the specified range as the
   * <code>data</code> property.
   */
  onLoadRange?: ComponentProps<typeof ListView>['onLoadRange'];
};

/**
 * A SelectMultiple is a dropdown list that supports multiple selection and search filtering.
 */
export const SelectMultiple = forwardRef(
  <K extends string | number, D extends Record<string, any>>(
    {
      ariaDescribedBy,
      assistiveText,
      data: propData,
      helpSourceLink,
      helpSourceText,
      isDisabled: propIsDisabled,
      isLoading: propIsLoading,
      isReadonly: propIsReadonly,
      isRequired,
      isRequiredShown,
      itemText,
      label,
      labelEdge: propLabelEdge,
      labelStartWidth: propLabelStartWidth,
      messages,
      onCommit,
      onFilter,
      onLoadRange: propOnLoadRange,
      placeholder,
      textAlign: propTextAlign,
      userAssistanceDensity: propUserAssistanceDensity,
      valueItems,
      variant = 'default',
      virtualKeyboard
    }: Props<K, D>,
    ref?: Ref<FocusableHandle>
  ) => {
    const {
      isDisabled: isFormDisabled,
      isReadonly: isFormReadonly,
      labelEdge: formLabelEdge,
      labelStartWidth: formLabelStartWidth,
      textAlign: formTextAlign,
      userAssistanceDensity: formUserAssistanceDensity
    } = useFormContext();
    // default to FormContext values if component properties are not specified
    const isDisabled = propIsDisabled ?? isFormDisabled;
    const isReadonly = propIsReadonly ?? isFormReadonly;
    const labelEdge = propLabelEdge ?? formLabelEdge;
    const labelStartWidth = propLabelStartWidth ?? formLabelStartWidth;
    const textAlign = propTextAlign ?? formTextAlign;
    const userAssistanceDensity = propUserAssistanceDensity ?? formUserAssistanceDensity;

    const {
      enabledElementRef,
      focusProps,
      isFocused: origIsFocused,
      readonlyElementRef
    } = useFocusableTextField<HTMLInputElement, HTMLDivElement>({ isDisabled, isReadonly, ref });

    const {
      collectionProps,
      data,
      dropdownArrowEventHandlers,
      dropdownEventHandlers,
      dropdownRef,
      hasSelectedValuesCount,
      inputEventHandlers,
      isDropdownAbove,
      isDropdownOpen,
      isDropdownSelectedOnlyView,
      isFocused,
      isUserFiltering,
      mainFieldRef,
      mouseProps,
      onLoadRange,
      searchText,
      selectedValuesCountProps,
      userInput
    } = useSelectMultiple<K, D>({
      data: propData,
      inputRef: enabledElementRef as MutableRef<HTMLInputElement>,
      isDisabled,
      isFocused: origIsFocused,
      isReadonly,
      onCommit,
      onFilter,
      onLoadRange: propOnLoadRange,
      valueItems
    });

    // The incoming propIsLoading tells us that we are in a loading state, but we don't want to
    // show the loading indicator until after a delay, because showing it immediately could result
    // in unwanted flashing.  The isLoading var below will be set to true by useLoadingIndicatorTimer
    // after the delay, when the timer expires, at which point we should show the loading indicator.
    // If propIsLoading is set to false before the timer expires, then the timer will be cancelled
    // and no loading indicator will be shown.
    // (ListView detects its loading state by checking data === null.  While it would be nice for
    // us to be consistent and check valueItems === null, we need to use a separate isLoading prop.
    // If valueItems === null triggers our loading state, then the selections in the dropdown
    // are cleared for that period of time, which is undesirable.)
    const isLoading = useLoadingIndicatorTimer(propIsLoading ?? false);

    const {
      baseId,
      formFieldContext,
      inputProps,
      labelProps,
      textFieldProps,
      userAssistanceProps
    } = useTextField({
      ariaDescribedBy,
      isDisabled,
      isFocused,
      isLoading,
      isReadonly,
      labelEdge,
      messages,
      styleVariant: variant,
      value: valueItems !== undefined && valueItems.length > 0 ? true : undefined
    });

    const dropdownId = `${baseId}-dropdown`;

    const displayValue = useDisplayValues(itemText, valueItems);

    const labelComp = labelEdge !== 'none' ? <Label {...labelProps}>{label}</Label> : undefined;

    const fieldLabelProps = {
      label: labelEdge !== 'none' ? labelComp : undefined,
      labelEdge: labelEdge !== 'none' ? labelEdge : undefined,
      labelStartWidth: labelEdge !== 'none' ? labelStartWidth : undefined
    };

    const ariaLabel = labelEdge === 'none' ? label : undefined;

    const inlineUserAssistance =
      isDisabled || isReadonly ? (
        // save space for user assistance if density is 'efficient', even though we don't
        // render user assistance for disabled or readonly fields
        userAssistanceDensity !== 'efficient' ? undefined : (
          <InlineUserAssistance
            userAssistanceDensity={userAssistanceDensity}
            {...userAssistanceProps}
          />
        )
      ) : (
        <InlineUserAssistance
          assistiveText={assistiveText}
          fieldLabel={label}
          helpSourceLink={helpSourceLink}
          helpSourceText={helpSourceText}
          messages={messages}
          isRequiredShown={isRequiredShown}
          userAssistanceDensity={userAssistanceDensity}
          {...userAssistanceProps}
        />
      );

    const translations = useTranslationBundle<BundleType>('@oracle/oraclejet-preact');

    if (isReadonly) {
      return (
        <FormFieldContext.Provider value={formFieldContext}>
          <ReadonlyTextField
            role="presentation"
            inlineUserAssistance={inlineUserAssistance}
            onBlur={focusProps.onfocusout}
            onFocus={focusProps.onfocusin}
            {...fieldLabelProps}>
            <ReadonlyTextFieldInput
              ariaDescribedBy={ariaDescribedBy}
              ariaLabel={ariaLabel}
              ariaLabelledBy={labelProps.id}
              as="div"
              elementRef={readonlyElementRef}
              hasInsideLabel={label !== undefined && labelEdge === 'inside'}
              textAlign={textAlign}
              value={displayValue}
            />
          </ReadonlyTextField>
        </FormFieldContext.Provider>
      );
    }

    const isDataFetched = data !== undefined && data !== null && !isBeforeDataFetch<K, D>(data);
    const hasInsideLabel = labelComp !== undefined && labelEdge === 'inside';
    const liveRegionText =
      isDropdownOpen && isDataFetched
        ? data?.totalSize === 0
          ? translations.select_noMatchesFound()
          : data?.totalSize === 1
          ? translations.select_oneMatchFound()
          : data?.sizePrecision === 'exact'
          ? translations.select_sizeMatchesFound({ TOTAL_SIZE: `${data?.totalSize}` })
          : translations.select_sizeOrMoreMatchesFound({ TOTAL_SIZE: `${data?.totalSize}` })
        : '';

    const mainContent = (
      <Fragment>
        <TextFieldInput
          ariaAutoComplete="list"
          ariaControls={dropdownId}
          ariaExpanded={isDropdownOpen}
          ariaLabel={ariaLabel}
          autoComplete="off"
          hasInsideLabel={hasInsideLabel}
          inputRef={enabledElementRef}
          isRequired={isRequired}
          // onCommit={handleCommit}
          {...inputEventHandlers}
          placeholder={placeholder}
          role="combobox"
          spellcheck={false}
          textAlign={textAlign}
          type={isMobile() ? (virtualKeyboard === 'auto' ? 'search' : virtualKeyboard) : undefined}
          value={isUserFiltering ? userInput : displayValue}
          {...inputProps}
        />
        <LiveRegion>{liveRegionText}</LiveRegion>
      </Fragment>
    );

    const dropdownArrow = (
      <DropdownArrow
        isDisabled={isDisabled}
        size={hasInsideLabel ? 'md' : 'sm'}
        {...dropdownArrowEventHandlers}
      />
    );
    const selectedValuesCount = valueItems?.length;
    const endContent = isLoading ? undefined : !hasSelectedValuesCount ? (
      dropdownArrow
    ) : (
      <Flex align="center" justify="center" gap={[0, '1x']}>
        <SelectedValuesCount
          accessibleLabel={label}
          count={selectedValuesCount}
          isDisabled={isDisabled}
          isSelected={isDropdownSelectedOnlyView}
          {...selectedValuesCountProps}
        />
        {dropdownArrow}
      </Flex>
    );

    const dropdownInlineUserAssistance =
      isDropdownOpen &&
      !isDropdownAbove &&
      !isDisabled &&
      !isReadonly &&
      (assistiveText || helpSourceLink) &&
      (userAssistanceDensity === 'efficient' || userAssistanceDensity === 'reflow') ? (
        <DropdownUserAssistance
          assistiveText={assistiveText}
          fieldLabel={label}
          helpSourceLink={helpSourceLink}
          helpSourceText={helpSourceText}
          userAssistanceDensity={userAssistanceDensity}
        />
      ) : undefined;

    const isEmptyResults = isDataFetched && data?.totalSize === 0;
    const dropdownListContent = isEmptyResults ? (
      <EmptyResults />
    ) : (
      <DefaultList<K, D>
        {...collectionProps}
        accessibleSummary={label ?? ''}
        data={data}
        itemText={itemText}
        onLoadRange={onLoadRange}
        searchText={
          isDropdownSelectedOnlyView ? undefined : isUserFiltering ? searchText : undefined
        }
        selectionMode="multiple"
      />
    );

    return (
      <Fragment>
        <FormFieldContext.Provider value={formFieldContext}>
          <TextField
            endContent={endContent}
            inlineUserAssistance={inlineUserAssistance}
            mainContent={mainContent}
            mainFieldRef={mainFieldRef}
            onBlur={focusProps?.onfocusout}
            onFocus={focusProps?.onfocusin}
            {...textFieldProps}
            {...fieldLabelProps}
            {...mouseProps}></TextField>
        </FormFieldContext.Provider>
        <Dropdown
          dropdownRef={dropdownRef}
          id={dropdownId}
          isOpen={
            isDropdownOpen &&
            data !== undefined &&
            (data === null || data.totalSize > 0 || isBeforeDataFetch<K, D>(data) || isEmptyResults)
          }
          anchorRef={mainFieldRef}
          {...dropdownEventHandlers}>
          <DropdownList
            hasBottomGap={dropdownInlineUserAssistance === undefined}
            isLoading={data === null}>
            {dropdownListContent}
          </DropdownList>
          {dropdownInlineUserAssistance}
        </Dropdown>
      </Fragment>
    );
  }
);
